import React from 'react'
import {useState, useEffect} from "react";
import {Link} from 'react-router-dom'
import {db, storage, reference, uBytes} from '../firebase-config'
import { getDatabase, ref, set } from "firebase/database";
import {child, get } from "firebase/database";

import { getDownloadURL } from '@firebase/storage';
import Header from './Header';

import { CKEditor } from '@ckeditor/ckeditor5-react';
import ClassicEditor from '@ckeditor/ckeditor5-build-classic';

import parse from 'html-react-parser'
const AddQuery = () => {
    const [editorContent, setEditorContent] = useState('')
    const[badgeColor, setBadgeColor] = useState("")
    const[badgeContent, setBadgeContent] = useState("")

    const [nombre, setNombre]=useState([])
    const [descripcion, setDescripcion]=useState("")

    const [imgUrl, setImgUrl] = useState(null)
 


const uploadDoc = async () =>{
 
    
      await fetch('https://demo.docsie.xyz/api/querys/subir', {
        method: 'POST',
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'Application/json'
        },
        body: JSON.stringify(
          { nombre: nombre,
            descripcion: descripcion,
            contenido: editorContent
        })
      })
      .then((r)=>{
        console.log(r)

      })
      .catch((error)=>{
        console.log(error)
      })


       
       
    

}

  useEffect(()=>{

  }, [])


    
        return (
            <div className="rich-text-editor">
                <div className={badgeColor=="green" ? "badge badge-green" : "badge badge-red"}>{badgeContent}</div>
                <h2>Subir Query</h2>
               <div className="editor">
                <div className="doc-data">
                <label htmlFor="nombre">Nombre</label>

                <input placeholder="Nombre" onChange={(event) =>{
                setNombre(event.target.value)
              }}></input>
               <label htmlFor="descripcion">Descripcion</label>

<input placeholder="descripcion" onChange={(event) =>{
setDescripcion(event.target.value)
}}></input>
              <label htmlFor="categorial">Categoria</label>
    
                </div>
               <CKEditor 
                editor={ClassicEditor}
                data={editorContent}
                config={{         
                  toolbar: ['heading', '|', 'bold', 'italic', 'blockQuote', 'codeBlocknpm run start', 'link', 'numberedList', 'bulletedList', 'imageUpload', 'insertTable',
                    'tableColumn', 'tableRow', 'mergeTableCells', 'mediaEmbed', '|', 'undo', 'redo']
                }}  
                onChange={(event, editor)=>{
                    const data = editor.getData()
                    setEditorContent(data)
                }}
                ></CKEditor>
                <div className={"rich-text-editor_buttons"}>
                <button className="button" onClick={()=>uploadDoc()}>Añadir Documentación</button>

                </div>
               </div>
              
            </div>
        )
    }
   

export default AddQuery
