import React from 'react'
import { useState } from 'react'

const ButtonAdd = () => {
    const [visibility, setVisibility] = useState('')
    return (
        <div className="buttonAdd">
                        <a className={visibility==true? 'visible' : ''} href="/documentacion/subir-documentacion">Añadir Documentación</a>
                        <a className={visibility==true? 'visible' : ''} href="/bug/addBug">Añadir Bug</a>

            <button onClick={()=>setVisibility(!visibility)}>{visibility==true? 'x' : '+'}</button>

        </div>
    )
}

export default ButtonAdd
