import React, { useEffect } from 'react'
import './Home.css'
import {Link} from 'react-router-dom'
import { useState } from 'react'
import Header from './Header'
import { query } from 'firebase/database'
const Home = (props) => {
const [path, setPath] = useState('/')
const [querys, setQuerys] = useState([])
const getQuerys = ()=>{
  try {
   fetch("https://demo.docsie.xyz/api/querys/")
   .then(res=>res.json())
   .then((result)=>{
     var newQuerys = []
     console.log("prueba api")
     result.docs.forEach(el=>{
       console.log(el)
       newQuerys.push({nombre: el.nombre, descripcion: el.descripcion, creadoPor: el.creado_por})
     })
     setQuerys(newQuerys)
     console.log(querys)

   })}
   catch(error){
     console.log(error)
   } 
   
  }
useEffect(()=>{
  getQuerys()

  }, [])

    


    return (
      <div>
         
      <div className="induccion-section">
        
          <div className="posts">
              <div className="posts-header">
                <h2>Querys</h2>
                <p>En esta pagina se muestra el procedimiento para realizar diferentes acciones mediante la base de datos</p>
                 
              </div>
              <div className="card-grid">
               {querys.map(el=>(
                  <div className="card">
                    <Link to={'/querys/'+el.nombre.toLowerCase().replace(/ /g, '-')}>
                    <h3>{el.nombre}</h3>
                  <p>{el.descripcion}</p>
                  <span>{el.creadoPor}</span>
                    </Link>
                 
              </div>
               ))}
               
              </div>
          </div>
      </div>
      </div>
    )

    }
export default Home
