import React from 'react'
import { useParams } from 'react-router-dom'
import { useEffect } from 'react'
import { useState } from 'react'
import parse from 'html-react-parser'
import './QueryDetailed.css'
const QueryDetailed = () => {
    const [query, setQuery] = useState()
    const [content, setContent] = useState("")
    const [contentHtml, setContentHtml] = useState("a")

    const {id} = useParams()
    const getQuerys = async ()=>{
        try {
        await fetch(`http://localhost:3050/querys/${id}`)
         .then(res=>res.json())
         .then((result)=>{
           var newQuerys = []
           console.log("prueba api")
           result.docs.forEach(el=>{
             console.log(el)
             newQuerys.push({nombre: el.nombre, descripcion: el.descripcion, creadoPor: el.creado_por})
             setContent(el.contenido)
           })
           setQuery(newQuerys)
           console.log(query)
      
         })}
         catch(error){
           console.log(error)
         } 
         console.log(content)
    
        }
 useEffect(() => {
            
    getQuerys()
    const contentDecoded = Buffer.from(content, 'base64').toString('ascii')
    setContentHtml(String(contentDecoded))
        }, [content])
    return (
        <div className='query-detailed'>
            {parse(contentHtml)}
        </div>
    )
}

export default QueryDetailed
